<template>
  <div class="list">
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="name" label="客户姓名" width="200">
            </el-table-column>
            <el-table-column prop="phone" label="联系电话" width="250">
            </el-table-column>
            <el-table-column prop="idNum" label="身份证号" width="350">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="edit(scope.row._id)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="del(scope.row._id)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
  </div>
</template>
<script>
export default {
  data() {
      return {
          tableData:[]
      }
  },
  methods: {
      getData() {
          this.$axios.get('getClient').then(res => {
              if(res.code == 200) {
                  this.tableData = res.data
              }
          })
      },
      del(id) {
            this.$confirm('是否删除该客户？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.post('delClient',{id}).then(res => {
                    if(res.code == 200) {
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                        });
                        setTimeout(() => {
                            this.$router.go(0)                            
                        })
                    }
                })              
            }).catch(() => {
                this.$message({
                type: 'info',
                message: '已取消删除'
                });          
            });
      },
      edit(id) {
          this.$router.push({path:'/clients/update',query:{id}})
      }
  },
  created() {
      this.getData()
  }
}
</script>
<style scoped>
.list{
    margin: 20px;
}
</style>



